const CreateAksaraPageTemplate = (aksara) => `
    <article class="aksara-item" tabindex="0">
        <div class= "aksara-img"> 
            <img src= "${aksara.imgUrl}">
        </div>
        <div class= "aksara-body"> 
            <h2>${aksara.label || "-"}</h2>
        </div>
    </article>
`;

const showLoadingSpinner = () => {
  document.getElementById("loadingSpinner").style.display = "block";
};

const hideLoadingSpinner = () => {
  document.getElementById("loadingSpinner").style.display = "none";
};

export { CreateAksaraPageTemplate, showLoadingSpinner, hideLoadingSpinner };
